import React from 'react';
import ContentDecorator from '../../components/textDecorator/ContentDecorator';
import TextDecorator from '../../components/textDecorator/TextDecorator';
import { LandingContext } from '../../contexts/LandingContext';
import { txt, stl } from '../../lang';
import styled, { keyframes } from 'styled-components';
import MetaButton from '../../components/buttons/MetaButton';
import { useWindowSize } from '../../hooks/useWindowSize';
import Card from '../../components/cards/Card';

const ButtonArea = styled.div`
    display:-webkit-box;
    display:-ms-flexbox;
    display:flex;
    -webkit-box-orient:horizontal;
    -webkit-box-direction:normal;
    -ms-flex-direction:row;
            flex-direction:row;
    -webkit-box-align:center;
    -ms-flex-align:center;
            align-items:center;
    -webkit-box-pack:start;
    -ms-flex-pack:start;
            justify-content:flex-start;
    width:100%;
    @media(max-width:768px){
        margin-left:0;
    }
`
const ButtonEffectAnimation = keyframes`
    0%{
        opacity:0;
    }
    40%{
        opacity:0.9;
    }
    60%
    {
        opacity: 0.2;
    }
    70%
    {
        opacity: 0.9;
    }
    80%
    {
        opacity: 0.2;
    }


    100%{
        opacity: 1;
    }
`
const ButtonEffect = styled.div`
    -webkit-animation: ${ButtonEffectAnimation} 0.27s both  ease-in ;
    animation: ${ButtonEffectAnimation} 0.27s both  ease-in ;
    -webkit-animation-delay: ${props => props.time}s;
            animation-delay: ${props => props.time}s;
    opacity:1;
`

const Container = styled.div`
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: ${props => props.position};
    -ms-flex-pack: ${props => props.position};
            justify-content: ${props => props.position};
    height: calc(100vh - 227px);
`;


export default function Section1() {
    const size = useWindowSize();
    const [scaleX, setScaleX] = React.useState(1);
    const [scaleY, setScaleY] = React.useState(1);
    const [fontSizeTop, setFontSizeTop] = React.useState(54);
    const [fontSizeDown, setFontSizeDown] = React.useState(64);
    const [containerPosition, setContainerPosition] = React.useState('center')
    const { lang } = React.useContext(LandingContext);
    const content = txt('section1', 'body', lang);
    const fontFamily = lang != "en" ? size.width < 490 ? 'SwissraD' : 'Israr-Syria' : 'Kusanagi';
    var discordStyle = stl(lang, 'discordButton');
    const discordClick = (e) => {
        e.preventDefault();
        // window.location.href = "https://discord.gg/metagate";
        window.open("https://discord.gg/metagate");
    }
    const twitterClick = (e) => {
        e.preventDefault();
        // window.location.href = txt('buttons', 'twitterButtonLink', lang);
        window.open(txt('buttons', 'twitterButtonLink', lang));
    }

    React.useEffect(() => {
        setScaleX(size.width / 1440);
        setScaleY(size.height / 1024);
        if (size.width < 490) {
            if (lang != 'en') {
                setFontSizeTop(34);
                setFontSizeDown(29);
                setContainerPosition('flex-end');
            } else {
                setFontSizeTop(25);
                setFontSizeDown(29);
                setContainerPosition('flex-end');
            }
        } else {
            setFontSizeTop(54 * scaleX * scaleY);
            setFontSizeDown(64 * scaleX * scaleY);
            setContainerPosition('center')
        }
    })
    const cardContent = 'Our Aetheryan Genesis collection is the first glimpse into the framework and foundation that fuels our ultramodern metaverse engine filled with limitless opportunities.'
    return (
        <Container position={containerPosition}>
            <TextDecorator direction={'up'} fontSize={fontSizeTop} content={txt('section1', 'welcome', lang)} fontFamily={fontFamily} width='fit-content'></TextDecorator>
            <TextDecorator direction={'down'} fontSize={fontSizeDown} content='metagate' width='fit-content'></TextDecorator>
            <ContentDecorator scaleX={scaleX} scaleY={scaleY} fontSize={24} lang={lang} content={content} width={634}></ContentDecorator>
            <ButtonArea>
                <div onClick={discordClick}>
                    <ButtonEffect time={0.8}>
                        <MetaButton marginTop={'5vh'} marginLeft={discordStyle.marginLeft} marginRight={discordStyle.marginRight} background={'#FF1C6E'} icon="discord" />
                    </ButtonEffect>
                </div>
                <div onClick={twitterClick}>
                    <ButtonEffect time={1}>
                        <MetaButton marginTop={'5vh'} background={'white'} icon="twitter" />
                    </ButtonEffect>
                </div>
            </ButtonArea>
        </Container>
        // <Card brandImg={'assets/images/brand/brand.svg'} title='aetherya' content={cardContent} ></Card>
    )
}