import React, { useContext } from 'react';
import styled, { keyframes } from 'styled-components';
import Header from '../components/header/Header';
import HeaderV1 from '../components/header/HeaderV1';
import HeaderV2 from '../components/header/HeaderV2';
import Footer from '../components/Footer/Footer';
import Section1 from '../pages/landingPages/Section1';
import { LandingContext } from '../contexts/LandingContext'
import { txt, stl, nextLang, getRTL } from '../lang';
import SideBar from '../components/sidebar/SideBar';

const ScrollTransition = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
   
    margin-left: 18px;
    @media (max-width: 1120px) {
        margin-left: 10px
    }
    @media (max-width: 768px) {
        display: none !important;
    }
`;

const ScrollUpsideBarAnimation = keyframes`
    0%{
        clip-path: inset(175px 0px 0px 0px);
    }
    100%{
        clip-path: inset(0px 0px 0px 0px);
    }
`

const ScrollUpsideBar = styled.svg`
    animation: ${ScrollUpsideBarAnimation} 1s ease 1;
    animation-fill-mode: forwards;
`

const RectButtonsAppearAnimation = keyframes`
    0%{
        transform: translateX(-100%);
        -webkit-transform: translateX(-100%);
    }
    100%{
        transform: translateX(0px);
        -webkit-transform: translateX(0px);
    }
`

const RectButtonsTransition = styled.div`
    transform: translateX(-100%);
    display: flex;
    flex-direction: column;
    animation: ${RectButtonsAppearAnimation} 0.8s ease 1;
    animation-fill-mode: forwards;
`

const RectButtonsContainer = styled.div`
    height: fit-content;
    overflow: hidden;
`

const Transition = styled.div`
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    ${props => props.isRTL && 'direction : rtl'};
    background-image: -webkit-linear-gradient(top,rgba(0,0,0,0), rgba(0,0,0,1)), url(${props => props.background});
    background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0),rgba(0, 0, 0, 0), rgba(0,0,0,1)), url(${props => props.background});
    background-attachment: fixed;
    background-size: cover;
    background-position: center bottom;
    background-repeat: no-repeat;
    transition-property: background-image;
    transition-duration: 1s;
    transition-timing-function: ease-in;

    @media (max-width: 735px ) {
        background-image: -webkit-linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0,0,0,1)), url(${props => props.background});
        background-position: ${props => props.isRTL ? 'left 31% bottom 100px' : '69% -100px'};
    }

    @media (min-aspect-ratio: 16/9 ) {
        background-image: -webkit-linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0,0,0,1)), url('assets/images/backgrounds/section1-16-9.png');
        background-position: ${props => props.isRTL ? 'right 0% bottom 0px' : 'left 0% bottom 0px'};
    }

    /* Maximum aspect ratio
    @media (max-aspect-ratio: 3/2) {
        background-size: auto 100%;  
    }

    @media (aspect-ratio: 1/1) {
        background-size: auto 100%; 
    } */
`


const FooterAnimation = keyframes`
    0%{
        -webkit-transform: translateY(100%);
                transform: translateY(100%);
    }
    100% {
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }
`

const FooterContainer = styled.div`
    width: 91.68vw;
    padding-left: 4.16vw;
    padding-right: 4.16vw;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
            justify-content: center;
    direction: initial;
    -webkit-transform: translateY(100%);
    -ms-transform: translateY(100%);
            transform: translateY(100%);
    -webkit-animation: ${FooterAnimation} 1s ease 1;
            animation: ${FooterAnimation} 1s ease 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    margin-bottom: 10px;
    @media (max-width: 735px) {
        margin-bottom: 10px;
    }
`

const HeaderContainer = styled.div`
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: end;
    -ms-flex-align: end;
            align-items: flex-end;
    overflow: hidden;
    direction: initial;
    margin-top: 30px;
    @media (max-width: 490px) {
        margin-top: 11px;
    }
`

const BodyContainer = styled.div`
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -ms-flex-direction: row;
            flex-direction: row;
    width: 100vw;
    overflow: hidden;
    @media (max-width: 735px) {
        height: calc(100vh - 183px);
    }
`

const HeaderBox = styled.div`
    -webkit-box-flex: 0;
    -ms-flex: 0 0 auto;
    flex: 0 0 auto;
    overflow: hidden;
`;

const BodyBox = styled.div`
    -webkit-box-flex: 1;
    -ms-flex: 1 0 auto;
    flex: 1 0 auto;
    overflow: auto;
`;

const FooterBox = styled.div`
    -webkit-box-flex: 0;
    -ms-flex: 0 0 auto;
    flex: 0 0 auto;
    overflow: hidden;
    width: 100%;
`

export default function LayoutV1({children}) {
    const { lang } = useContext(LandingContext);
    let isRTL = getRTL(lang);
    const scrollBody = React.useRef();
    const backgroundImages = [
        'assets/images/backgrounds/section1.png',
        // 'assets/images/backgrounds/section2.png',
        // 'assets/images/backgrounds/section4.png',
        // 'assets/images/backgrounds/section8.png',
        // 'assets/images/backgrounds/section8.png',
        // 'assets/images/backgrounds/section8.png',
        // 'assets/images/backgrounds/section8.png',
        // 'assets/images/backgrounds/section9.png',
        // 'assets/images/backgrounds/section10.png',
    ]

    const backgroundImagesRTL = [
        'assets/images/backgrounds/section1-rtl.png',
        // 'assets/images/backgrounds/section2.png',
        // 'assets/images/backgrounds/section4.png',
        // 'assets/images/backgrounds/section8.png',
        // 'assets/images/backgrounds/section8.png',
        // 'assets/images/backgrounds/section8.png',
        // 'assets/images/backgrounds/section8.png',
        // 'assets/images/backgrounds/section9.png',
        // 'assets/images/backgrounds/section10.png',
    ]

    const [activeIndex, setActiveIndex] = React.useState(0);
    const RectButton = ({ index, activeIndex, setActiveIndex }) => {
        const moveTo = () => {
            setActiveIndex(index)
            scrollBody.current.scrollTo(0, scrollBody.current.scrollHeight / backgroundImages.length * index);
        }
        return (
            <svg onClick={() => moveTo()} width="1.38vw" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg" style={{ margin: '0.34vw', cursor: 'pointer' }}>
                <rect x="0.707107" y="10" width="13.1421" height="13.1421" transform="rotate(-45 0.707107 10)" fill={activeIndex == index ? "#FF1C6E" : 'white'} fillOpacity="0.3" stroke={activeIndex == index ? "#FF1C6E" : 'white'} />
            </svg>
        )
    }

    const handleScroll = (e) => {
        e.stopPropagation()
        setActiveIndex(Math.round(scrollBody.current.scrollTop / scrollBody.current.scrollHeight * backgroundImages.length))
    }

    return (


        <Transition background={isRTL ? backgroundImagesRTL[activeIndex] : backgroundImages[activeIndex]} isRTL={isRTL}>

            <HeaderBox>

                <HeaderContainer>
                    <HeaderV1></HeaderV1>
                </HeaderContainer>
            </HeaderBox>

            <BodyBox>
                <BodyContainer>

                    <div style={{ width: '4.16vw' }}>
                        {/* <ScrollTransition>

                            <div style={{ marginBottom: '1.07vh' }}>
                                <ScrollUpsideBar height="18vh" viewBox="0 0 10 185" fill="none" xmlns="http://www.w3.org/2000/svg">
                                    <path opacity="0.5" d="M5.00001 0.669876L9.33014 5L5.00001 9.33012L0.669881 5L5.00001 0.669876ZM4.25001 185L4.25001 5L5.75001 5L5.75001 185L4.25001 185Z" fill="white" />
                                </ScrollUpsideBar>
                            </div>

                            <RectButtonsContainer>
                                <RectButtonsTransition>
                                    {
                                        backgroundImages.map((item, index) => {
                                            return (
                                                <RectButton index={index} activeIndex={activeIndex} setActiveIndex={setActiveIndex} key={index + 'rect_button'}></RectButton>
                                            )
                                        })
                                    }
                                </RectButtonsTransition>
                            </RectButtonsContainer>

                            <div style={{ marginTop: '1.07vh' }}>
                                <ScrollUpsideBar id="upperarrow" height="18vh" viewBox="0 0 10 185" fill="none" xmlns="http://www.w3.org/2000/svg" style={{ transform: 'rotate(180deg)' }}>
                                    <path opacity="0.5" d="M5.00001 0.669876L9.33014 5L5.00001 9.33012L0.669881 5L5.00001 0.669876ZM4.25001 185L4.25001 5L5.75001 5L5.75001 185L4.25001 185Z" fill="white" />
                                </ScrollUpsideBar>
                            </div>
                        </ScrollTransition> */}

                        <SideBar></SideBar>
                    </div>

                    {/* <div style={{ width: '91.68vw', overflow: 'auto' }} ref={scrollBody} onScroll={(e) => handleScroll(e)}> */}
                    <div style={{ width: '91.68vw', overflow: 'auto' }}>    
                        {children}
                    </div>
                    <div style={{ width: '4.16vw' }}>
                    </div>

                </BodyContainer>
            </BodyBox>
            <FooterBox>
                <FooterContainer>
                    <Footer></Footer>
                </FooterContainer>
            </FooterBox>
        </Transition>
    )
}

