import HeaderButton from '../buttons/HeaderButton';
import React from 'react';
import styled, { keyframes } from 'styled-components';
import TwitterButton from '../buttons/TwitterButton';
import DiscordButton from '../buttons/DiscordButton';
import TelegramButton from '../buttons/TelegramButton';
import MenuButton from '../buttons/MenuButton';
import HeaderLeftSide from './HeaderLeftSide';
const MoveLeft = keyframes`
  0%{
    transform: translateX(calc(100% - 40px));
  }
  100% {
    transform: translateX(0px);
  }
`

const MoveLeftSmall = keyframes`
  0%{
    transform: translateX(10%);
  }
  100% {
    transform: translateX(-14%);
  }
`
const MetaLogoLeft = styled.svg`
  transform: translateX(100%);
  animation: ${MoveLeft} 1s 0.5s ease-in-out;
  animation-fill-mode: forwards;

`

const Expand = keyframes`
  0%{
    width: 60px;
    height: 60px;
    clip-path: polygon(50% 0, 100% 50%, 50% 100%, 0 50%);
  }
  95% {
    clip-path: polygon(50% 0, 100% 50%, 50% 100%, 0 50%);
  }
  100% {
    width: 202px;
    height: 202px;
    clip-path: polygon(0 0, 100% 0%, 100% 100%, 0 100%);

  }
`
const ExpandSmall = keyframes`
  0%{
    width: 43px;
    height: 43px;
    clip-path: polygon(50% 0, 100% 50%, 50% 100%, 0 50%);
  }
  95% {
    clip-path: polygon(50% 0, 100% 50%, 50% 100%, 0 50%);
  }
  100% {
    width: 134px;
    height: 134px;
    clip-path: polygon(0 0, 100% 0%, 100% 100%, 0 100%);

  }
`

const MetaLogoContent = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  align-content: center;
  width: 60px;
  height: 60px;
  clip-path: polygon(50% 0, 100% 50%, 50% 100%, 0 50%);
  animation: ${Expand} 1.5s 0.5s ease;
  animation-fill-mode: forwards;
`

const LogoActionAnimation = keyframes`
  0%{
    opacity: 0;
  }
  3% {
    opacity: 0.6;
  }
  6%{
      opacity: 0.05;
  }
  8% {
      opacity: 0.6;
  }
  11% {
      opacity: 0.6;
  }
  13% {
      opacity: 0.05
  }
  16% {
      opacity: 0.05;
  }
  31%{
      opacity: 0.6;
  }
  50% {
      opacity: 0.9;
  }
  80%{
      opacity: 0.9;
  }
  100% {
      opacity: 1;
  }

`

const LogoAction = styled.div`
    animation: ${LogoActionAnimation} 2s ease-in-out 1;
    animation-fill-mode: forwards;
`

const HeaderMiddleLeft = styled.svg`
    width: 408px;
`

const HeaderMiddleRight = styled.svg`
    width: 408px;
    transform: scale(-1, 1);
`

const MetaLogoContentContainer = styled.div`
    width: calc(100vw - 1220px);
    min-width: 232px;
    height: 78px;
    display: flex;
    justify-content: center;
    align-items: center;
    transform: translateY(-50%);

`

const MetaLogoContainer = styled.div`
    display: flex;
    flex-direction: column;
    width: calc(100vw - 1220px);
    min-width: 232px;
`

const MetaLogoSVG = styled.svg`
    width: 202px;
    height: 69px;
`

